// page.tsx
"use client";

import { useState } from "react";

export default function ChatPage() {
  const [inputMessage, setInputMessage] = useState("");
  const [messages, setMessages] = useState([
    { text: "Hello! How can I assist you?", sender: "support", time: "2:00 PM" },
  ]);

  const handleSendMessage = () => {
    if (inputMessage.trim()) {
      setMessages([
        ...messages,
        { text: inputMessage, sender: "user", time: new Date().toLocaleTimeString() },
      ]);
      setInputMessage("");
    }
  };

  return (
    <div>
      {/* The layout (including the floating chat and chat window) will be rendered here */}
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-3xl font-bold">ðŸ’¬ Chat Support</h2>
      </div>

      {/* Chat History */}
      <div className="p-4">
        {messages.map((msg, index) => (
          <div key={index} className={`mb-3 ${msg.sender === "user" ? "text-right" : "text-left"}`}>
            <div
              className={`inline-block px-4 py-2 rounded-lg ${msg.sender === "user" ? "bg-blue-100" : "bg-green-100"}`}
            >
              {msg.text}
            </div>
            <div className="text-xs text-gray-500">{msg.time}</div>
          </div>
        ))}
      </div>

      {/* Message Input */}
      <div className="flex items-center p-3 border-t">
        <input
          type="text"
          placeholder="Type a message..."
          className="w-full p-2 border rounded-l-lg"
          value={inputMessage}
          onChange={(e) => setInputMessage(e.target.value)}
        />
        <button onClick={handleSendMessage} className="bg-blue-600 text-white p-2 rounded-r-lg">
          Send
        </button>
      </div>
    </div>
  );
}
